import React, { Component } from 'react'

export class Content extends Component {
    render() {
        return (
            <div className="item col-sm-12 col-md-6 col-lg-3">
                <img src={this.props.img} alt="" />
                <h4>{this.props.title}</h4>
                <h6>  {this.props.by}</h6>
            </div>
        )
    }
}

export default Content
